import React from 'react';
import Helmet from 'react-helmet';
import styled from '@emotion/styled';
import { Link, graphql } from 'gatsby';
import kebabCase from 'lodash/kebabCase';

import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Chip from '@material-ui/core/Chip';
import TagIcon from '@material-ui/icons/LocalOffer';
import CategoryIcon from '@material-ui/icons/Label';
import PrevIcon from '@material-ui/icons/NavigateBefore';
import NextIcon from '@material-ui/icons/NavigateNext';

import Bio from '../components/bio';
import Layout from '../components/layout';
import { Container } from '../components/commons';
import { rhythm, scale } from '../utils/typography';
import SEO from '../components/SEO';

const styles = theme => ({
  chipLink: {
    textDecoration: 'none',
    cursor: 'pointer',
  },
  chip: {
    margin: theme.spacing.unit,
    cursor: 'pointer',
  },
});

function waitForGlobal(name, timeout = 300) {
  return new Promise((resolve, reject) => {
    let waited = 0;

    function wait(interval) {
      setTimeout(() => {
        waited += interval;
        // some logic to check if script is loaded
        // usually it something global in window object
        if (window[name] !== undefined) {
          return resolve();
        }
        if (waited >= timeout * 1000) {
          return reject(new Error('Timeout'));
        }
        wait(interval * 2);
      }, interval);
    }

    wait(30);
  });
}

class BlogPostTemplate extends React.Component {
  componentDidMount() {
    waitForGlobal('MathJax').then(() => {
      top.MathJax.Hub.Config({
        tex2jax: {
          inlineMath: [['$', '$'], ['\\(', '\\)']],
          displayMath: [['$$', '$$'], ['[', ']']],
          processEscapes: true,
          processEnvironments: true,
          skipTags: ['script', 'noscript', 'style', 'textarea', 'pre'],
          TeX: {
            equationNumbers: { autoNumber: 'AMS' },
            extensions: ['AMSmath.js', 'AMSsymbols.js'],
          },
        },
      });
    });
    if (top.MathJax != null) {
      top.MathJax.Hub.Queue(['Typeset', top.MathJax.Hub]);
    }
  }

  render() {
    const post = this.props.data.markdownRemark;
    const siteTitle = this.props.data.site.siteMetadata.title;
    const siteDescription = post.excerpt;
    const { previous, next } = this.props.pageContext;
    const { classes } = this.props;

    return (
      <Layout
        location={this.props.location}
        title={siteTitle}
      >
        <SEO
          title={post.frontmatter.title}
          description={post.frontmatter.description}
        />
        <Helmet>
          <script
            type="text/javascript"
            src="https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.5/MathJax.js?config=TeX-AMS-MML_HTMLorMML"
            async
          />
        </Helmet>
        <Container height="auto" innerWidth="720px" css={{ marginTop: '40px' }}>
          <h1>{post.frontmatter.title}</h1>
          <p
            style={{
              ...scale(-1 / 5),
              display: 'block',
              marginBottom: rhythm(1),
            }}
          >
            {post.frontmatter.date}
          </p>

          <img
            src={post.frontmatter.cover.childImageSharp.resolutions.src}
            alt="Cover"
          />
          <div dangerouslySetInnerHTML={{ __html: post.html }} />

          <hr />
          <div>
            <Chip
              icon={<CategoryIcon />}
              label={post.frontmatter.category}
              className={classes.chip}
              clickable
              color="primary"
            />
            {post.frontmatter.tags.map(tag => (
              <Link
                to={`/tags/${kebabCase(tag)}`}
                className={classes.chipLink}
              >
                <Chip
                  key={tag}
                  icon={<TagIcon />}
                  label={tag}
                  variant="outlined"
                  clickable
                  className={classes.chip}
                />
              </Link>
            ))}
          </div>
          <hr />
          <ul
            style={{
              display: 'flex',
              flexWrap: 'wrap',
              justifyContent: 'space-between',
              listStyle: 'none',
              padding: 0,
              margin: 0,
            }}
          >
            <li>
              {previous && (
                <Link
                  to={previous.fields.slug}
                  rel="prev"
                  style={{
                    textDecoration: 'none',
                  }}
                >
                  <Button variant="contained">
                    <PrevIcon />{previous.frontmatter.title}
                  </Button>
                </Link>
              )}
            </li>
            <li>
              {next && (
                <Link
                  to={next.fields.slug}
                  rel="next"
                  style={{
                    textDecoration: 'none',
                  }}
                >
                  <Button variant="contained">
                    {next.frontmatter.title} <NextIcon />
                  </Button>
                </Link>
              )}
            </li>
          </ul>
        </Container>
      </Layout>
    );
  }
}

export default withStyles(styles)(BlogPostTemplate);

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt
      html
      frontmatter {
        title
        category
        tags
        description
        date(formatString: "MMMM DD, YYYY")
        cover {
          childImageSharp {
            resolutions(width: 1000) {
              src
            }
          }
        }
      }
    }
  }
`;
